@import "../../_globalColor";

.experience-card {
  position: relative;
  background-color: $lightBackground2;
  box-shadow: $lightBoxShadowDark 0px 10px 30px -15px;
  border-radius: 10px;
  border: 1px solid $lightBorder1;
}

.experience-card-dark {
  position: relative;
  background-color: $darkBackground;
  box-shadow: $lightBoxShadowDark 0px 10px 30px -15px;
  border-radius: 10px;
  border: 1px solid $lightBorder1;
}

.experience-card:hover {
  box-shadow: $lightBoxShadowDark 0px 20px 30px -10px;
  transform: translateY(-5px);
  transition: all 0.3s ease;
}

.experience-banner {
  background-position: center;
  background-repeat: no-repeat;
  background-size: 150%;
  background: $experienceCardBannerLG;
  border-radius: 10px 10px 0px 0px;
  -webkit-filter: blur(0px);
  -moz-filter: blur(0px);
  -o-filter: blur(0px);
  -ms-filter: blur(0px);
  filter: blur(0px);
  height: 11rem;
  display: flex;
  align-items: flex-end;
  justify-content: center;
}

.experience-blurred_div {
  position: absolute;
  background: $experienceCardBlurredDivLG;
  -webkit-filter: blur(5px);
  -moz-filter: blur(5px);
  -o-filter: blur(5px);
  -ms-filter: blur(5px);
  filter: blur(5px);
  height: 11rem;
  top: 0;
  left: 0;
  width: 100%;
  border-radius: 10px 10px 0px 0px;
}

.experience-div-company {
  position: absolute;
  background: transparent;
  height: 9rem;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.experience-text-company {
  text-align: center;
  padding: 1.5rem;
  margin: 0;
  color: $lightBackground1;
  font-size: 25px;
  text-align: center;
  font-weight: 700;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.experience-roundedimg {
  position: absolute;
  object-fit: cover;
  left: 0;
  right: 0;
  top: 7rem;
  margin-left: auto;
  margin-right: auto;
  width: 8rem;
  height: 8rem;
  box-shadow: 0 0.5rem 1rem $lightBoxShadowDarker;
  border-radius: 50%;
}

.experience-text-role {
  text-align: center;
  color: $textColor;
  font-weight: 700;
  font-size: 25px;
  margin: 0px;
  padding-top: 1.5rem;
  line-height: normal;
}
.dark-mode-text {
  color: $textColorDark !important;
}
.experience-text-date {
  text-align: center;
  color: $textColor;
  font-size: 20px;
  margin: 0px;
  padding-top: 1rem;
  font-weight: 600;
}

.experience-text-desc {
  text-align: center;
  display: block;
  overflow: visible;
  text-overflow: initial;
}

.experience-text-details {
  padding: 1.5rem;
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  height: calc(100% - 11rem - 2rem);
}

.experience-bullets-container {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.6s ease-in-out;
  
  &.expanded {
    max-height: 1000px; /* Adjust this value as needed */
  }
  
  ul {
    margin-top: 0.5rem;
    padding-left: 1.5rem;
    
    li {
      margin-bottom: 0.5rem;
      line-height: 1.5;
      font-size: 0.95rem;
    }
  }
}

.experience-card, .experience-card-dark {
  position: relative;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  cursor: pointer;
  height: auto;
  min-height: 600px;
  
  &:hover {
    transform: translateY(-5px);
    box-shadow: $lightBoxShadowDark 0px 20px 30px -10px;
  }
  
  &.expanded-card {
    margin-bottom: 10px;
  }
}

.expand-indicator {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 10px;
  color: $textColor;
  font-size: 0.9rem;
  
  span {
    margin-right: 5px;
  }
  
  i {
    font-size: 0.8rem;
    transition: transform 0.3s ease;
  }
}

.dark-mode-text .expand-indicator {
  color: $textColorDark;
}

/* Media Query */

@media (max-width: 1380px) {
  .experience-text-role {
    padding-top: 0.5rem;
    font-size: 22px;
  }
  .experience-text-company {
    font-size: 22px;
  }
  .experience-text-date {
    font-size: 18px;
  }
  .experience-roundedimg {
    width: 6.5rem;
    height: 6.5rem;
  }
}

@media (max-width: 768px) {
  .experience-text-role {
    font-size: 22px;
  }
  .experience-text-company {
    font-size: 22px;
  }
  .experience-text-date {
    font-size: 18px;
  }
  .experience-text-desc {
    font-size: 16px;
  }
  
  .experience-card, .experience-card-dark {
    cursor: pointer;
    min-height: 400px;
    
    &:hover {
      transform: none;
      box-shadow: $lightBoxShadowDark 0px 10px 30px -15px;
    }
    
    &.expanded-card {
      box-shadow: $lightBoxShadowDark 0px 20px 30px -10px;
    }
  }
  
  .expand-indicator {
    padding: 8px 0;
    font-size: 1rem;
  }
}
